type TrackEvent<TId extends TrackEventId, TAction extends string> = {
    id: TId;
    action: TAction;
    label?: string;
    domain?: string;
};

type LoginEvent = TrackEvent<'login', 'ok' | 'ko'>;
type ScreenViewEvent = TrackEvent<'screen_view', View>;
type TabLoadEvent = TrackEvent<'tab_load', string>;
type OfferLinkedEvent = TrackEvent<'offer_linked', string>;
type ClickEvent = TrackEvent<'click', string>;
type ProductViewEvent = TrackEvent<'product_view', string>;
type ExtensionLoadEvent = TrackEvent<'extension_load', string>;
type VoucherUnlockedEvent = TrackEvent<'voucher_unlocked', string>;

interface CreateAnalyticsEvent {
    (event: LoginEvent): void;
    (event: ScreenViewEvent): void;
    (event: TabLoadEvent): void;
    (event: OfferLinkedEvent): void;
    (event: ClickEvent): void;
    (event: ProductViewEvent): void;
    (event: ExtensionLoadEvent): void;
    (event: VoucherUnlockedEvent): void;
}
export interface IAnalytics {
    setAccount(account: { trackId: string }): void;
    productView(page: { url: string; tabId: number }): void;
    pageView(page: { url: string }): void;
    extensionLoad(): void;
    screenView(screen: { view?: View; tabId: number }): void;
    event: CreateAnalyticsEvent;
    voucherUnlocked(shop: { shopUrl: string }): void;
}

function clearCookies() {
    document.cookie.split(';').forEach((c) => {
        document.cookie = c
            .replace(/^ +/, '')
            .replace(
                /=.*/,
                '=;expires=' + new Date(0).toUTCString() + ';path=/' + ';domain=' + document.domain,
            );
    });
}

function loadGA(): IAnalytics {
    const trackId = import.meta.env.VITE_GA_TRACKID || '';
    // let commonConfig: amplitudeJS.Config = {
    //     disableCookies: true,
    // };

    if (
        import.meta.env.VITE_NODE_ENV !== 'production' ||
        import.meta.env.VITE_INCLUDE_UTILS === '1'
    ) {
        // commonConfig = {
        //     ...commonConfig,
        //     platform: `Web-development`,
        // };
    }

    // amplitude.init(import.meta.env.VITE_AMPLITUDE_APIKEY as string, undefined, commonConfig);
    // const window = globalThis.window ?? {};
    // (window as any)._gaq = (window as any)._gaq || [];
    let initialized = false;

    const sendEvent: CreateAnalyticsEvent = (eventData) => {
        const { id, action, label } = eventData;
        const sendData: { [k: string]: string } = { ...eventData };
        delete sendData.id;

        logger.debug(`Analytics.E.${id}`, sendData);
        if (!initialized) return;
        // amplitude.logEvent(id, sendData);
        if (
            import.meta.env.VITE_NODE_ENV !== 'production' ||
            import.meta.env.VITE_INCLUDE_UTILS === '1'
        )
            return;
        // (window as any)._gaq.push(['_trackEvent', id, `${action || id}`, label]);
    };
    const sendPageView = (url: string) => {
        logger.debug(`Analytics.PV`, { url });
        if (!initialized) return;
        // amplitude.logEvent('screenView', { screen: url });
        if (
            import.meta.env.VITE_NODE_ENV !== 'production' ||
            import.meta.env.VITE_INCLUDE_UTILS === '1'
        )
            return;
        // (window as any)._gaq.push(['_trackPageview', url]);
    };

    const api: IAnalytics = {
        setAccount: (account) => {
            if (!initialized) {
                logger.error('Setting analytics account without initialization');
                return;
            }
            logger.debug('Analytics.A.setAccount', { account });
            if (!trackId) {
                logger.warn('Setting analytics account without trackId');
                return;
            }
            if (
                import.meta.env.VITE_NODE_ENV !== 'production' ||
                import.meta.env.VITE_INCLUDE_UTILS === '1'
            )
                return;
            // (window as any)._gaq.push(['_setAccount', account.trackId]);
        },
        extensionLoad: () => {
            initialized = true;
            api.setAccount({ trackId });
            sendEvent({
                id: 'extension_load',
                action: browser.runtime.id || 'ext_id',
            });
        },
        productView: (page) => {
            const { url, tabId } = page;
            sendEvent({
                id: 'product_view',
                action: url,
                label: '' + tabId,
                // future regex /(?<web>w{3})+\.(?<domain>(?<subdomain>\w+)\.?(?<mainDomain>\w+\.\w{2,3}))/ > still needs to be more precise
                domain: url.slice(0, url.search(/\/[^\/w]/)).replace(/https:\/\/www\./, ''),
            });
        },
        pageView: (page) => {
            logger.debug('Analytics.P.pageView', page);
            sendPageView(page.url);
        },
        voucherUnlocked: ({ shopUrl }) => {
            sendEvent({ id: 'voucher_unlocked', action: shopUrl });
        },
        screenView: (screen) => {
            const { view: rawView, tabId } = screen;
            const view = `/${rawView || ''}`;
            logger.debug('Analytics.P._screenView', { view, tabId });
            sendPageView(view);
        },
        event: (data) => {
            logger.debug('Analytics.E._event', data);
            sendEvent(data as any);
        },
    };

    return api;
}

export const analytics = loadGA();
export default analytics;
