import { v4 as uuidv4 } from 'uuid';
import { setAuthValues } from '~B/utils/auth';
import browser from '../browser/browser';
import { aesGcmDecrypt, aesGcmEncrypt } from '../utils/cypher';
import { DataRepository } from './storage.api';
import { AuthStateData, UserInformation } from './storage.types';
import { SerpShops } from '~B/types';
// import { setAuthValues } from './utils/auth';

export const AuthData = new DataRepository<AuthStateData>('chollo_assist_auth_data', {
    lastReqAuth: null,
    lastReqAuthBody: null,
    reqAuth: null,
    reqAuthBody: null,
    reqAuthExp: 0,
    reqAuthRxp: 0,
});
export const schemaData = new DataRepository<{
    version: number | null;
}>('chollo_assist_data_model', { version: null }, browser.storage.sync);

export const ClientData = new DataRepository<{
    hash: string;
}>('chollo_assist_client_data', { hash: uuidv4() }, browser.storage.sync);

export const AffiliateData = new DataRepository<{ shopName: string; lastTimeTracked: number }[]>(
    'chollo_assist_affiliate_data',
    [],
    browser.storage.local,
);
export const privatePolicyData = new DataRepository<{ isPolicyAuth: boolean; ppId?: number }>(
    'chollo_assist_private_policy',
    { isPolicyAuth: false },
    browser.storage.sync,
);

export const serpShopsData = new DataRepository<SerpShops['data']>('chollo_assist_serp', {
    shops: [],
});
export const UserData = new DataRepository<UserInformation | null, string>(
    'chollo_assist_user_data',
    null,
    browser.storage.sync,

    {
        async intoInner(data) {
            const iv = (await ClientData.get()).hash;
            const toEncrypt = JSON.stringify(data);
            return await aesGcmEncrypt(toEncrypt, iv);
        },
        async fromInner(data) {
            if (typeof data === 'string') {
                const iv = (await ClientData.get()).hash;
                const decrypted = await aesGcmDecrypt(data, iv);
                return JSON.parse(decrypted);
            }
            if (typeof data === 'object') {
                const userData = data as UserInformation;
                if (userData?.username && userData?.password) {
                    await this.set(data);
                    return data;
                } else {
                    await this.set(null);
                    return null;
                }
            }
            return data;
        },
        async onClear(data) {
            await setAuthValues(null);
        },
    },
);
