import manifest from '~/manifest';
import logger from '~/ui/utils/logger';

import { UserData, UserInformation } from '~B/storage';

export const extension_manifest = manifest;

export async function collectMetrics() {
    try {
        const { username } = ((await UserData.get()) as UserInformation) ?? { username: 'unlogin' };
        let merchantDomain, productUrl, platform;
        const url = new URL(location.href);
        const version = extension_manifest.version;

        merchantDomain = url.host.replace(/^w+\./, '');
        productUrl = url.href;
        platform = navigator.userAgent;

        return username === 'unlogin'
            ? {
                  merchantDomain,
                  productUrl,
                  platform,
                  version,
              }
            : {
                  username,
                  merchantDomain,
                  productUrl,
                  platform,
                  version,
              };
    } catch (error) {
        logger.error((error as Error).message);
    }
}
