/**
 * Logs a message and save it for later use
 * @param {string} message An explanatory message
 * @param {object} A plain object containing more details
 * @param {'info' | 'warn' | 'debug' | 'error' | 'trace'} Log level
 */
function log(message: string, details: any, level: 'info' | 'trace' | 'debug' | 'warn' | 'error') {
    if (import.meta.env.VITE_NODE_ENV === 'production' || import.meta.env.VITE_NODE_ENV === 'stage')
        return;

    if (details?._args?.length === 0) {
        delete details._args;
    }

    console[level](message, details);

    if (details.exception) {
        details.exception = details.exception.message;
    }
}

/**
 * Log a trace message
 * @param {string} message An explanatory message
 * @param {object} A plain object containing more details
 */
export function trace(message: string, details = {}, ...args: any[]) {
    return log(message, { details: details || {}, _args: args }, 'trace');
}

/**
 * Log a debug message
 * @param {string} message An explanatory message
 * @param {object} A plain object containing more details
 */
export function debug(message: string, details = {}, ...args: any[]) {
    return log(message, { details: details || {}, _args: args }, 'debug');
}

/**
 * Log an info message
 * @param {string} message An explanatory message
 * @param {object} A plain object containing more details
 */
export function info(message: string, details = {}, ...args: any[]) {
    return log(message, { details: details || {}, _args: args }, 'info');
}

/**
 * Log a warning message
 * @param {string} message An explanatory message
 * @param {object} A plain object containing more details
 */
export function warn(message: string, details = {}, ...args: any[]) {
    return log(message, { details: details || {}, _args: args }, 'warn');
}

/**
 * Log an error message
 * @param {string} message An explanatory message
 * @param {object} A plain object containing more details
 */
export function error(message: string, details = {}, ...args: any[]) {
    return log(message, { details: details || {}, _args: args }, 'error');
}

export default {
    trace,
    debug,
    info,
    log: info,
    warn,
    error,
};
