import logger from '~/ui/utils/logger';

import { AuthData, AuthStateData, DecodedToken, UserData } from '~B/storage';
import { DataRepository } from '~B/storage/storage.api';

export async function setAuthValues(
    token: string | null,
    authRepo: DataRepository<AuthStateData> = AuthData,
): Promise<void> {
    const authData = await authRepo.get();

    authData.lastReqAuth = authData.reqAuth || authData.lastReqAuth;
    authData.reqAuth = token;

    if (authData.reqAuth === null) {
        /*  authData.reqAuth = null;
        authData.reqAuthExp = 0; */
        authRepo.clear();
        return;
    }

    // Get the body contained in the token.
    authData.lastReqAuthBody = authData.reqAuthBody || authData.lastReqAuthBody;
    authData.reqAuthBody = JSON.parse(atob(authData.reqAuth.split('.')[1])) as DecodedToken;

    authData.reqAuthExp = authData.reqAuthBody.exp * 1000;
    authData.reqAuthRxp = authData.reqAuthBody.rxp * 1000;

    await authRepo.set(authData);
}

export async function isAuthorizationExpired(date?: Date): Promise<boolean> {
    const authData = await AuthData.get();
    authData.lastReqAuth = authData.reqAuth || authData.lastReqAuth;

    return authData.reqAuthExp < (date || new Date()).valueOf();
}

export async function isAuthorizationRenewable(): Promise<boolean> {
    return (await getDecodedToken())?.ttl > 0;
}
export async function isAuthorizationRefreshable(): Promise<boolean> {
    // mirar rxp si el servidor lo envía sino usamos el tiempo por defecto
    const authData = await AuthData.get();
    logger.log(authData.reqAuthRxp, {
        now: Date.now(),
        then: authData.reqAuthRxp > Date.now(),
    });
    return (
        (await isAuthorizationRenewable()) &&
        (authData?.reqAuthExp || 0) < Date.now() &&
        Date.now() < (authData?.reqAuthRxp || 0)
    );
}
export async function getAccessToken(): Promise<string | null> {
    const authData = await AuthData.get();

    return authData?.reqAuth;
}

export async function getLastAccessToken(): Promise<string | null> {
    return (await AuthData.get())?.lastReqAuth || null;
}

export async function getDecodedToken(): Promise<DecodedToken> {
    return (
        (await AuthData.get())?.reqAuthBody || {
            exp: 0,
            iat: 0,
            sat: 0,
            sid: '',
            ttl: 0,
            rxp: 0,
            identity: {
                clientId: '',
                version: '',
                extensionId: '',
            },
        }
    );
}
export async function userHasAccess(): Promise<boolean> {
    let authData = await AuthData.get();
    const hasAccess: boolean = !!(authData?.reqAuth && !(await isAuthorizationExpired())); // here's the thing baby !!!!!

    if (!hasAccess) {
        await setAuthValues(null);
    }

    return hasAccess;
}

export async function userIsLoggedIn(): Promise<boolean> {
    const hasAccess = await userHasAccess();
    const userDataExists = !!(await UserData.get());
    return hasAccess && userDataExists;
}
