import { decode, encode } from '@msgpack/msgpack';

const wrappedFetch = fetch;
const baseUrl = import.meta.env.VITE_ENDPOINT || '$$$$$$$$';

enum RequestMethod {
    GET = 'GET',
    POST = 'POST',
    PUT = 'PUT',
    PATCH = 'PATCH',
    DELETE = 'DELETE',
    UNKNOWN = 'UNKNOWN',
}

export interface Message {
    headers: { [k: string]: string };
    url: string;
    body: any;
    method: RequestMethod;
}

export function packMessage<T>(msg: T): string {
    return btoa(String.fromCharCode(...encode(msg)));
}

export function unpackMessage<T>(msg: string): T {
    const bString = atob(msg);
    const bArray = new Uint8Array(bString.length);
    for (let i = 0; i < bString.length; i++) {
        bArray[i] = bString.charCodeAt(i);
    }
    return decode(bArray) as T;
}

interface FetcherResponseBase<T> {
    headers: { [k: string]: string };
    status: number;
    payload: T;
    ok: boolean;
    networkStatus: 'OK' | 'KO' | 'ERROR';
    json: () => Promise<T>;
    statusText: string;
}

interface FetcherResponseOK<T extends any> extends FetcherResponseBase<T> {
    networkStatus: 'OK';
}

interface FetcherResponseKO extends FetcherResponseBase<any> {
    networkStatus: 'KO';
}

interface FetcherResponseNetwork extends FetcherResponseBase<null> {
    networkStatus: 'ERROR';
}

type FetcherResponse<T extends any> =
    | FetcherResponseKO
    | FetcherResponseOK<T>
    | FetcherResponseNetwork;

interface Fetcher {
    <T extends any>(url: string, options?: RequestInit): Promise<FetcherResponse<T>>;
}

function getHeaders(headers?: HeadersInit): Record<string, string> {
    if (Array.isArray(headers)) {
        return Object.fromEntries(headers);
    }

    if (headers instanceof Headers) {
        return Object.fromEntries((headers as any).entries() as string[][]);
    }

    return headers || {};
}

function getUrl(url: string): string {
    const tempSplit = url.split(baseUrl);
    if (tempSplit.length > 1) {
        return tempSplit[1];
    }
    return url;
}

function getMethod(method?: string): RequestMethod {
    if (!method) return RequestMethod.UNKNOWN;

    const tMethod = method as RequestMethod;

    return Object.values(RequestMethod).includes(tMethod)
        ? RequestMethod[tMethod]
        : RequestMethod.UNKNOWN;
}

export const fetcher: Fetcher = async function fetcher<T extends any>(
    url: string,
    options?: RequestInit,
    isMetric?: boolean,
): Promise<FetcherResponse<T>> {
    // create message

    const message: Message = {
        headers: getHeaders(options?.headers),
        url: url.endsWith('/metrics') ? '/metric' : getUrl(url),
        method: getMethod(options?.method),
        body: options?.body || null,
    };

    // pack message

    const packedMessage = packMessage(message);

    // send message and wait

    const response = await wrappedFetch(isMetric ? url : baseUrl + '/service', {
        method: 'POST',
        body: JSON.stringify({
            payload: packedMessage,
        }),
        headers: {
            'Content-Type': 'application/json',
        },
    })
        .then(async (response) => {
            const payload = unpackMessage((await response.json()).payload);
            return {
                ...(response.ok
                    ? ({
                          networkStatus: 'OK',
                          json: () => Promise.resolve(payload as T),
                          payload: payload as T,
                      } as FetcherResponseOK<T>)
                    : ({
                          networkStatus: 'KO',
                          json: () => Promise.resolve(payload as any),
                          payload: payload as any,
                      } as FetcherResponseKO)),
                ok: response.ok,
                headers: getHeaders(response.headers),
                status: response.status,
                statusText: response.statusText,
            } as FetcherResponseKO | FetcherResponseOK<T>;
        })
        .catch(
            () =>
                ({
                    headers: {},
                    status: 600,
                    statusText: 'Cannot fetch',
                    networkStatus: 'ERROR',
                    payload: null,
                    ok: false,
                    json: () => Promise.resolve(null),
                } as FetcherResponseNetwork),
        );

    if (import.meta.env.VITE_NODE_ENV !== 'production') {
        console.group(`Comm [${message.method} ${message.url}]`);

        console.groupCollapsed(`Request [Id: ${message.headers['X-Request-Id']}]`);
        console.debug('Headers: ');
        console.table(message.headers);
        console.debug('Endpoint: ', message.url);
        console.debug('Method: ', message.method);
        console.debug('Body: ', JSON.parse(message.body));
        console.groupEnd();

        console.groupCollapsed(`Response [Ok:${response.ok}] [Status:${response.status}]`);
        console.debug('Ok? & Status', response.ok, response.status);
        console.debug('Headers', getHeaders(response.headers));
        console.debug('Body', response.payload);
        console.groupEnd();

        console.groupEnd();
    }

    return response;
};
