import { BehaviorSubject, firstValueFrom, Observable, Subject } from 'rxjs';
import {
    distinctUntilChanged,
    filter,
    map,
    raceWith,
    share,
    shareReplay,
    skip,
} from 'rxjs/operators';
import { backgroundFetcher } from '~B/backgroundFetch';
import browser from '~B/browser/browser';
import { collectMetrics } from '~F/app/api/metrics/utils';
import logger from '../logger';
import { APIContext, APIHandler, APIResource, Fetcher } from './api.types';
import { HTTPMethod, makeRequest } from '~B/utils/request';
import manifest from '~/manifest';

export interface metricsOptions {
    method: 'POST';
    body: {
        name: string;
        data?: {
            username?: string;
            merchantDomain: string;
            productUrl: string;
            platform?: string;
            targetUrl?: string;
            productTitle?: string;
            productImageUrl?: string;
            version?: string;
        };
    };
}
const apiHandlerCache: {
    [k: string]: APIHandler<any, any>;
} = {};

export function createAPI<
    TResource extends any,
    Methods extends {
        [k: string]: (this: APIContext, input: any) => Promise<any> | void;
    },
>({
    name,
    read,
    default: defaultResource,
    methods,
    endpoint,
}: {
    name: string;
    endpoint?: string;
    default?: () => TResource;
    methods: Methods;
    read?: (this: APIContext) => Promise<TResource>;
}): APIHandler<typeof methods, TResource> {
    const onSync$ = new Subject<TResource>();
    // Hits cache
    if (name in apiHandlerCache) {
        return apiHandlerCache[name];
    }
    let connected = false;

    //! API handling code
    const apiModuleId = 'bapi';
    const apiId = `${apiModuleId}/${name}`;
    const questionId = `${apiId}-q`;
    const answerId = `${apiId}-a`;
    const syncId = `${apiId}-s`;
    const keys = Object.keys(methods) as (keyof Methods)[];
    const thisContext: APIContext = {
        baseURL: '',
        endpoint: endpoint || '',
        fetch,
        async metric(options: metricsOptions) {
            try {
                const res = await this.fetch('', options);
                logger.log(`Metric request ${options.body.name}`, res);
            } catch (error) {
                logger.log(`Error on ${options.body.name} metric`, error);
            }
        },
    };

    function setContext(newContext: APIContext) {
        Object.assign(thisContext, newContext);
    }
    const apiHandler: APIHandler<Methods, TResource> = {
        default: defaultResource,
        name,
        id: apiId,
        read: read.bind(thisContext),
        connect() {
            if (connected) return;
            const newAPI = createConnectedAPI(apiHandler);
            apiHandler.read = newAPI.read;
            apiHandler.methods = newAPI.methods;
            connected = true;
        },
        onSync$: onSync$.pipe(
            share({
                resetOnError: false,
                resetOnComplete: false,
                resetOnRefCountZero: false,
            }),
        ),
        syncData: (d) => {
            logger.debug(`${name}.syncData: `, { data: d });
            onSync$.next(d);
        },
        setContext: (setter) => setContext(setter(thisContext)),
        init(fetcher: Fetcher) {
            setContext({
                ...thisContext,
                fetch: (input, options, sender) => {
                    const endpoint = `${thisContext.baseURL.replace(
                        /\/*$/,
                        '',
                    )}/${thisContext.endpoint.replace(/^\/*/, '')}`;
                    if (this.name.includes('metrics')) {
                        return makeRequest({
                            endpoint: '',
                            body: options?.body,
                            method: options?.method as HTTPMethod,
                            isMetric: true,
                        });
                    }
                    return fetcher(`${endpoint.replace(/^\/*/, '')}${input}`, options, sender);
                },
            });
        },
        methods: keys.reduce((acc, k) => {
            acc[k] = methods[k].bind(thisContext) as any;
            return acc;
        }, {} as APIHandler<Methods, TResource>['methods']),
        actions: {
            syncId,
            answerId,
            questionId,
            q: {
                ...keys.reduce((acc, k: keyof Methods) => {
                    acc[k] = `${questionId}.${k}`;
                    return acc;
                }, {} as { [k in keyof Methods]: string }),
                read: `${questionId}.read`,
            },
            a: {
                ...keys.reduce((acc, k: keyof Methods) => {
                    acc[k] = `${answerId}.${k}`;
                    return acc;
                }, {} as { [k in keyof Methods]: string }),
                read: `${answerId}.read`,
            },
        },
    };

    apiHandler.init(fetch);
    // Save to cache
    apiHandlerCache[name] = apiHandler;
    return apiHandler;
}

const connectedAPISyncStore: { [k: string]: (data: any) => void } = {};

function registerListener() {
    browser.runtime.onMessage.addListener(({ type, payload }) => {
        connectedAPISyncStore[type]?.(payload);
    });
}
registerListener();

function createConnectedAPI<Methods, TResource>(
    apiHandler: APIHandler<Methods, TResource>,
): APIHandler<Methods, TResource> {
    // Do no connect if testing
    if (import.meta.env.VITE_NODE_ENV === 'test') {
        logger.log('Background fetcher loaded');

        apiHandler.init(backgroundFetcher);
        return apiHandler;
    }

    connectedAPISyncStore[apiHandler.actions.syncId] = apiHandler.syncData;

    const methods = Object.keys(apiHandler.actions.q).reduce((ob, k: any) => {
        ob[k] = async (data: any) => {
            let payload;
            switch (apiHandler.name) {
                // calculate collect metrics payload

                case 'metrics:views':
                case 'metrics:pepper':
                case 'metrics:alerts':
                case 'metrics:serp':
                    payload = {
                        data: { ...(await collectMetrics()), ...data.data },
                        name: data.name,
                    };
                    break;

                case 'metrics:install':
                    payload = {
                        data: {
                            platform: navigator.userAgent,
                            version: manifest.version,
                        },
                        name: data.name,
                    };
                    break;

                default:
                    payload = data;
            }
            return browser.runtime
                .sendMessage({
                    type: (apiHandler.actions.q as any)[k],
                    payload: payload,
                })
                .catch(() => {
                    return { error: new Error('NO_RESPONSE') };
                })
                .then((d: { [k: string]: any }) => {
                    if (!d) {
                        throw new Error('API_ENGINE_ERROR');
                    }
                    if (d?.error) {
                        Object.setPrototypeOf(d.error, Error);
                        throw d.error;
                    }
                    return d?.payload;
                });
        };
        return ob;
    }, {} as any);

    return {
        ...apiHandler,
        read: methods['read'],
        methods,
    };
}

const apiCache: {
    [k: string]: APIResource<any, any>;
} = {};
export function createResource<APIMethods, TResource>(
    apiHandler: APIHandler<APIMethods, TResource>,
): APIResource<APIMethods, TResource> {
    if (!(apiHandler.name in apiCache)) {
        let api = apiHandler;
        apiCache[apiHandler.name] = innerCreateResource(api);
    }
    return apiCache[apiHandler.name] as APIResource<APIMethods, TResource>;
}

type APIObserverOnNextFn<TResource> = (resource: TResource) => void;
type APIObserverOnErrorFn = (error: Error) => void;
export interface IAPIObserver<TResource> {
    onNext: APIObserverOnNextFn<TResource>;
    onError: APIObserverOnErrorFn;
}
export interface IAPIObservable<TResource> {
    subscribe: (observer: IAPIObserver<TResource>) => () => void;
}

export interface IAPISubject<TResource>
    extends IAPIObserver<TResource>,
        IAPIObservable<TResource> {}

function observeIdentityFn(v: any) {}

export function createObserver<TResource>(
    onNext: APIObserverOnNextFn<TResource> = observeIdentityFn,
    onError: APIObserverOnErrorFn = observeIdentityFn,
): IAPIObserver<TResource> {
    return {
        onNext,
        onError,
    };
}
export function createSubject<TResource>(
    onNext: APIObserverOnNextFn<TResource> = observeIdentityFn,
    onError: APIObserverOnErrorFn = observeIdentityFn,
): IAPISubject<TResource> {
    let observers: IAPIObserver<TResource>[] = [];
    return {
        onNext: (d) => {
            onNext(d);
            observers.forEach((o) => o.onNext(d));
        },
        onError: (e) => {
            onError(e);
            observers.forEach((o) => o.onError(e));
        },
        subscribe: (observer) => {
            observers.push(observer);
            return () => {
                observers = observers.filter((ob) => ob !== observer);
            };
        },
    };
}

function innerCreateResource<APIMethods, TResource>(
    handler: APIHandler<APIMethods, TResource>,
): APIResource<APIMethods, TResource> {
    const { name, methods, init, read, default: defaultResource } = handler;
    let observers: IAPIObserver<TResource>[] = [];
    let resource = defaultResource();
    let status: 'success' | 'pending' | 'error' = 'pending';
    let error: any = null;
    let promise: Promise<any> | null = updateResource();
    let pendingTimeOut: NodeJS.Timeout | null;
    async function updateResource() {
        status = 'pending';
        try {
            const v = await read();
            status = 'success';
            return v;
        } catch (e) {
            error = e;
            status = 'error';
            throw e;
        }
    }
    function innerReadAll(): TResource {
        if (pendingTimeOut) clearTimeout(pendingTimeOut);
        switch (status) {
            case 'error':
                promise = null;
                pendingTimeOut = setTimeout(() => {
                    status = 'pending';
                    pendingTimeOut = null;
                }, 50);

                throw error;
            case 'pending':
                promise = promise || updateResource();
                throw promise;
            case 'success':
                return resource;
        }
    }
    function innerAutoUpdateAPI<TIn, TOut>(
        apiHandler: (element: TIn) => Promise<TOut>,
    ): (element: TIn) => Promise<TOut> {
        status = 'pending';
        return (element) =>
            (promise = apiHandler(element)
                .then(
                    (r) =>
                        (promise = updateResource().then(() => {
                            setTimeout(() => {
                                observers.forEach((o) => o.onNext(resource));
                            }, 0);
                            return r;
                        })),
                )
                .catch((e) => {
                    status = 'error';
                    error = e;
                    let observedError = e;
                    if (typeof observedError === 'string') {
                        observedError = new Error(observedError);
                    } else if (!(observedError instanceof Error)) {
                        observedError = new Error(`Unknown error: [${observedError}]`);
                    }
                    observers.forEach((o) => o.onError(observedError));
                }));
    }

    const methodKeys: (keyof APIHandler<APIMethods, TResource>['methods'])[] = Object.keys(
        methods,
    ) as any;

    return {
        ...methodKeys.reduce(
            (acc, k) => {
                acc[k] = (input: any) => {
                    if (typeof methods[k] === 'function') {
                        return innerAutoUpdateAPI(methods[k] as any)(input);
                    }
                    return Promise.resolve();
                };
                return acc;
            },
            {} as {
                [k in keyof APIHandler<APIMethods, TResource>['methods']]: any;
            },
        ),
        read: innerReadAll,
        onUpdate: (observer) => {
            observers.push(observer);
            return () => {
                observers = observers.filter((ob) => ob !== observer);
            };
        },
    };
}

export namespace ObservableAPIStore {
    const obAPIStore: { [k: string]: any } = {};
    const initSymbol = Symbol('initialValue');

    export type APIObservable<APIMethods, TResource> = {
        onError$: Observable<unknown>;
        onUpdate$: Observable<TResource>;
        readSync(): TResource;
        readForce(): Promise<TResource>;
        read(): Promise<TResource>;
        isLoading$: Observable<boolean>;
    } & {
        [Type in keyof APIMethods]: APIMethods[Type] extends () => Promise<infer TOut>
            ? () => Promise<TOut>
            : APIMethods[Type] extends (input: infer TIn) => Promise<infer TOut>
            ? (input: TIn) => Promise<TOut>
            : undefined;
    };

    export function get<APIMethods, TResource>(
        handler: APIHandler<APIMethods, TResource>,
        autoread = true,
    ): APIObservable<APIMethods, TResource> {
        if (obAPIStore[handler.name]) {
            return obAPIStore[handler.name];
        }
        const cachedValue$$ = new BehaviorSubject<TResource | Symbol>(initSymbol);
        const valueStream$ = cachedValue$$.pipe(
            filter((x) => x !== initSymbol),
            map((x) => x as TResource),
        );
        const isLoading$$ = new BehaviorSubject(false);
        const onError$$ = new BehaviorSubject<unknown>(undefined);

        const methodKeys = Object.keys(handler.methods) as (keyof APIHandler<
            APIMethods,
            TResource
        >['methods'])[];

        // sync with the handler
        handler.onSync$.subscribe(cachedValue$$);

        const read = async () => {
            try {
                isLoading$$.next(true);
                const val = await handler.read();
                cachedValue$$.next(val);
                return val;
            } catch (e) {
                onError$$.next(e);
                throw e;
            } finally {
                isLoading$$.next(false);
            }
        };

        const methods = methodKeys.reduce(
            (acc, k) => {
                acc[k] = async (input: any) => {
                    if (typeof handler.methods[k] === 'function') {
                        try {
                            isLoading$$.next(true);
                            const val = await (handler.methods[k] as any)(input);
                            await read();
                            return val;
                        } catch (e) {
                            onError$$.next(e);
                            throw e;
                        } finally {
                            isLoading$$.next(false);
                        }
                    }
                    return Promise.resolve();
                };
                return acc;
            },
            {} as {
                [k in keyof APIHandler<APIMethods, TResource>['methods']]: any;
            },
        );

        const obAPI: APIObservable<APIMethods, TResource> = {
            ...methods,
            read: () => firstValueFrom(valueStream$),
            readSync: () => {
                const value = cachedValue$$.getValue();
                if (value === initSymbol) {
                    if (onError$$.getValue() !== undefined) {
                        setTimeout(read, 100);
                        throw onError$$.getValue();
                    } else {
                        throw firstValueFrom(
                            valueStream$.pipe(
                                raceWith(
                                    obAPI.onError$.pipe(
                                        map((e) => {
                                            throw e;
                                        }),
                                    ),
                                ),
                            ),
                        );
                    }
                }
                return value as TResource;
            },
            readForce: read,
            isLoading$: isLoading$$.pipe(distinctUntilChanged(), shareReplay(1)),
            onUpdate$: valueStream$.pipe(shareReplay(1)),
            onError$: onError$$.pipe(skip(1), shareReplay(1)),
        };

        obAPIStore[handler.name] = obAPI;

        autoread && read().catch(() => {});

        return obAPI;
    }
}
