/* 
Describes browser object and its different uses depending of execution context.
@module src/background/browser
*/

import Browser from 'webextension-polyfill';
import logger from '../utils/logger';
import { BrowserStorage, TabInfo } from './browser.types';

let browserVar: browser.Browser;

if (import.meta.env.VITE_NODE_ENV === 'test') {
    browserVar = {
        extension: false,
        devtools: false,
        runtime: {
            id: 'ext_id',
            onMessage: createListenerHandler('runtime.onMessage'),
            sendMessage(message: any): Promise<any> {
                return Promise.resolve({});
            },
            reload() {
                logger.log('Mocked extension reload...');
            },
            onInstalled: createListenerHandler('runtime.onInstalled'),
            getManifest() {
                return {} as any;
            },
        },
        browserAction: {
            setBadgeText(arg) {
                logger.log('Setting badge text', arg);
            },
        },
        tabs: {
            sendMessage: () => Promise.resolve({}),
            onUpdated: createListenerHandler('tabs.onUpdated'),
            onRemoved: createListenerHandler('tabs.onRemoved'),
            create(properties) {
                logger.log('Created tab', properties);
                return Promise.resolve({} as TabInfo);
            },
            remove(tabIds) {
                logger.log('Removed tabs', tabIds);
                return Promise.resolve();
            },
        },
        storage: {
            local: createStorage({
                ['chollo_assist_auth_data']: {
                    exp: 0,
                    iat: 0,
                    sat: 0,
                    sid: '',
                    ttl: 0,
                    rxp: 0,
                    identity: {
                        clientId: '',
                        version: '',
                        extensionId: '',
                    },
                },
            }),
            sync: createStorage({
                ['chollo_assist_user_data']: {
                    password: 'choll0$',
                    username: 'luismedina',
                },
            }),
        },
    };
} else {
    browserVar = Browser;
}

export const browser = browserVar;
export default browser;

function createListenerHandler(name: string) {
    return {
        addListener(...args: any[]): any {
            logger.log('Registered listener on:', name);
        },
        removeListener(...args: any[]): any {
            logger.log('Unnregistered listener on:', name);
        },
    };
}
function createStorage(initial?: { [k: string]: any }): BrowserStorage {
    const storageCache = initial || {};
    return {
        set(items) {
            logger.log('set storage');
            Object.assign(storageCache, items);
            return Promise.resolve();
        },
        get(keys) {
            logger.log('get storage');
            return Promise.resolve(
                (keys as string[]).reduce((ob: any, k: string) => {
                    ob[k] = storageCache[k];
                    return ob;
                }, {} as any),
            );
        },
        clear() {
            for (const k in storageCache) {
                delete storageCache[k];
            }
            return Promise.resolve();
        },
        remove(keys) {
            for (const k of keys) {
                delete storageCache[k];
            }
            return Promise.resolve();
        },
    };
}
