export async function checkAuthRequest(authUrl: string, loginData?: LoginCredentials) {
    const headers = await getRequestHeaders();
    let authBody: AuthBody = {
        version: (await extension_manifest).version,
        extensionId: ext_id,
        clientId: await ext_client_id,
        ...loginData,
    };
    try {
        if (!auth_request || !auth_request.isPending()) {
            auth_request = fetch_retry<AuthResponse>(`${baseurl}/user/${authUrl}`, {
                headers,
                method: 'POST',
                body: JSON.stringify(authBody),
            });
        }

        const { accessToken, authUser } = await auth_request;

        if (isLoggedIn !== true) {
            isLoggedIn = true;
            analytics.event({ id: 'login', action: 'ok' });
        }

        await setAuthValues(accessToken);

        return authUser;
    } catch (e) {
        // caso extraordinario. serves says is not authorized by its credentials. reset ui and reset session.
        /*  if (authUrl === 'auth' && loginData && !(await getUserData())) {
         return getAuthorization();
     } */
        errorCapture(e);
        if (isLoggedIn !== false) {
            isLoggedIn = false;
            analytics.event({ id: 'login', action: 'ko' });
        }
        throw e;
    }
}
