import browser from '~B/browser/browser';
import { BrowserExtension, BrowserStorage } from '~B/browser/browser.types';
import logger from '~B/utils/logger';

type DataRepositoryAttachInternal<TData, TInner = TData> = {
    onClear: (this: DataRepository<TData, TInner>, data: TData) => Promise<void>;
    afterGet: (this: DataRepository<TData, TInner>, data: TData) => Promise<TData>;
    beforeSet: (this: DataRepository<TData, TInner>, data: TData) => Promise<TData>;
    intoInner: (this: DataRepository<TData, TInner>, data: TData) => Promise<TInner>;
    fromInner: (this: DataRepository<TData, TInner>, data: TInner) => Promise<TData>;
};

export type DataRepositoryAttach<TData, TInner> = Partial<
    DataRepositoryAttachInternal<TData, TInner>
>;
export class DataRepository<TData, TInner = TData> {
    dataKey: string;
    store: BrowserStorage;
    attach: DataRepositoryAttachInternal<TData, TInner>;
    defaultData: TData;

    private static DefaultAttach: DataRepositoryAttachInternal<any> = {
        async onClear() {},
        async afterGet(data) {
            return data;
        },
        async beforeSet(data) {
            return data;
        },
        async fromInner(data) {
            return data;
        },
        async intoInner(data) {
            return data;
        },
    };

    constructor(
        dataKey: string,
        defaultData: TData = {} as TData,
        storage = browser.storage.local,
        attach: DataRepositoryAttach<TData, TInner> = {},
    ) {
        this.dataKey = dataKey;
        this.store = storage;
        this.defaultData = defaultData;
        this.attach = Object.assign({}, DataRepository.DefaultAttach, attach);
    }

    async get(): Promise<TData> {
        const result = await this.store.get([this.dataKey]);

        const innerData = result[this.dataKey] as TInner;

        let data: TData | undefined;

        if (!result.hasOwnProperty(this.dataKey)) {
            if (Array.isArray(this.defaultData)) {
                const defaultData = [...this.defaultData] as unknown as TData;
                await this.set(defaultData);
                data = defaultData;
            }
            if (this.defaultData !== null && this.defaultData.constructor.name === 'Object') {
                const defaultData = { ...this.defaultData };
                await this.set(defaultData);
                data = defaultData;
            }

            await this.set(this.defaultData);
            data = this.defaultData;
        } else {
            try {
                data = await this.attach.fromInner.call(this, innerData);
            } catch (error) {
                logger.error('storage error');
                if ((error as Error).message.includes('Decrypt failed')) {
                    logger.error('storage error ------ removing everything');
                    await browser.storage.local.clear();
                    await browser.storage.sync.clear();
                    browser.runtime.reload();
                }
            }
        }

        if (this.defaultData !== null && this.defaultData.constructor.name === 'Object') {
            data = Object.assign({}, this.defaultData, data);
        }

        const finalData = await this.attach.afterGet.call(this, data);
        return finalData;
    }

    async set(data: TData): Promise<TData> {
        const adaptedData = await this.attach.beforeSet.call(this, data);
        const innerData = await this.attach.intoInner.call(this, adaptedData);
        await this.store.set({ [this.dataKey]: innerData });
        return adaptedData;
    }

    async clear(): Promise<void> {
        const data = await this.get();
        await this.attach.onClear.call(this, data);
        this.store.remove([this.dataKey]);
    }
}
