// TODO: This could be changed to a config
let log_level = 15;

const LEVELS = {
    trace: 1,
    debug: 1,
    info: 2,
    warn: 4,
    error: 8,
};

/**
 * Logs a message and save it for later use
 * @param {string} message An explanatory message
 * @param {object} A plain object containing more details
 * @param {'info' | 'warn' | 'debug' | 'error' | 'trace'} Log level
 */
function log(message, details, level = 'info') {
    if (import.meta.env.VITE_NODE_ENV === 'production') return;
    if (details?._args?.length === 0) {
        delete details._args;
    }

    if ((log_level & LEVELS[level]) > 0) {
        if (console.log) {
            // Avoid showing misleading error information to testers.
            console.log((level || 'info').toUpperCase(), message, details);
            // This is a better option if possible.
            // console[level](message, details);
        }
        // save_log_trace({
        //     level,
        //     message,
        //     details,
        //     timestamp: new Date().getTime(),
        // });
    }
}

/**
 * Log a trace message
 * @param {string} message An explanatory message
 * @param {object} A plain object containing more details
 */
export function trace(message, details = {}, ...args) {
    return log(message, { ...(details || {}), _args: args }, 'trace');
}

/**
 * Log a debug message
 * @param {string} message An explanatory message
 * @param {object} A plain object containing more details
 */
export function debug(message, details = {}, ...args) {
    return log(message, { ...(details || {}), _args: args }, 'debug');
}

/**
 * Log an info message
 * @param {string} message An explanatory message
 * @param {object} A plain object containing more details
 */
export function info(message, details = {}, ...args) {
    return log(message, { ...(details || {}), _args: args }, 'info');
}

/**
 * Log a warning message
 * @param {string} message An explanatory message
 * @param {object} A plain object containing more details
 */
export function warn(message, details = {}, ...args) {
    return log(message, { ...(details || {}), _args: args }, 'warn');
}

/**
 * Log an error message
 * @param {string} message An explanatory message
 * @param {object} A plain object containing more details
 */
export function error(message, details = {}, ...args) {
    return log(message, { ...(details || {}), _args: args }, 'error');
}

export default {
    trace,
    debug,
    info,
    log: info,
    warn,
    error,
};
